@import url(https://fonts.googleapis.com/css2?family=Pixelify+Sans:wght@400..700&display=swap);
body {
  font-family: "Pixelify Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  margin: 0;
}

.App {
  display: flex;
  justify-content: center;
  padding: 20px;
}

.zone-grid {
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
}

.boss-zone {
  border-radius: 5px;
  margin-bottom: 20px;
}

.boss-zone h2 {
  margin-top: 0;
}

.bosses {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  grid-gap: 10px;
}

.zone-name {
  font-size: 15px;
  margin: 0;
}

.boss {
    display: flex;
    align-items: center;
    border-radius: 5px;
    padding: 10px;
}

.boss img {
    width: 50px;
    height: 50px;
    border-radius: 10%;
    margin-right: 10px;
    background-color: #6b4b3c;
}

.boss-table {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;
    padding: 20px;
    background-color: #7c5645;
    border-radius: 20px;
    border: 10px solid #d19b52;
}


.boss-details {
    flex-grow: 1;
}

.boss-details h3 {
    margin-top: 0;
    margin-bottom: 5px;
}

.boss-name {
    font-weight: 500;
    font-size: 16px;
    margin: 0;
    color: white;
}

.boss-time {
    font-weight: 500;
    font-size: 15px;
    color: #acb0b1;
    margin: 0;
}


@media only screen and (max-width: 600px) {
    .boss-table {
        grid-template-columns: 1fr;
    }

    .bosses {
        grid-template-columns: 1fr;
    }
}
.App {
  background-image: url(/prequel-tool/static/media/s2-banner.4302a350.png);
  background-position: center 0;
  background-size: auto 24.5625rem;
  background-repeat: no-repeat;
  padding-top: 24.5625rem;
}


@media only screen and (max-width: 960px) {
  .App {
    background-image: url(/prequel-tool/static/media/m-s2-banner.afc04781.png);
    background-size: 100% 12.25rem;
    padding-top: 12.25rem;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.zone-title {
  margin: 2px;
}
